import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
import { dragonhatch } from './cDragonhatch';
const { ccclass, property } = _decorator;

@ccclass('dragonhatchAudio')
export class dragonhatchAudio extends Component {
    onLoad() {
    }

    start() {
    }

    private urlArr: string[] = ["sound/bgm_mg_main", "sound/bgm_mg_lv_01",
        "sound/bgm_mg_lv_02", "sound/bgm_mg_lv_03", "sound/bgm_mg_lv_04"];
    private bgmIndex: number;
    playBgm(index: number) {
        if (this.bgmIndex == index) return;
        this.bgmIndex = index;
        tgx.AudioMgr.inst.audio.stopMusic();
        if (index == 4) {
            tgx.AudioMgr.inst.audio.playMusic(EEFFECT.bgm_mg_lv_04_intro, () => {
                tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[4], ModuleDef.SLOTS_DRAGONHATCH)
            })
            return;
        }
        tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[index], ModuleDef.SLOTS_DRAGONHATCH)
    }

    /** */
    playReelLoop() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinBtn, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    private playReelEndBn: boolean = false;
    playReelEnd(isSpeed: boolean = false) {
        if (this.playReelEndBn) return;
        this.playReelEndBn = true
        this.scheduleOnce(() => {
            this.playReelEndBn = false;
        }, 0.01)
        if (isSpeed) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinReelStopQuick, ModuleDef.SLOTS_DRAGONHATCH);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.spinReelStopNormal, ModuleDef.SLOTS_DRAGONHATCH);
        }
    }
    /**spawn wild */
    playSpawnWild() {
        let arr: string[] = ["", "01", "02", "03"];
        let index: number = Math.floor(Math.random() * arr.length);
        index = index > arr.length - 1 ? arr.length - 1 : index;
        let str: string = EEFFECT.symWild + arr[index];
        tgx.AudioMgr.inst.audio.playEffect(str, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    playSelect() {
        if (dragonhatch.momModBn) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.payoutSelect + "02", ModuleDef.SLOTS_DRAGONHATCH);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.payoutSelect + "01", ModuleDef.SLOTS_DRAGONHATCH);
        }
    }
    /***/
    playRemove() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symShrink, ModuleDef.SLOTS_DRAGONHATCH);
    }

    /** */
    playBar(yellowBn: boolean = false) {
        if (yellowBn) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.barRiseB, ModuleDef.SLOTS_DRAGONHATCH);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.barRiseA, ModuleDef.SLOTS_DRAGONHATCH);
        }
    }
    /** */
    playhatch() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.barRiseHigher, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    playGreenMod() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonEarthAppear, ModuleDef.SLOTS_DRAGONHATCH, () => {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonEarthSkill, ModuleDef.SLOTS_DRAGONHATCH);
        });
    }
    /** */
    playBlueMod() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonWaterAppear, ModuleDef.SLOTS_DRAGONHATCH, () => {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonWaterSkill, ModuleDef.SLOTS_DRAGONHATCH, () => {
                tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonWaterSkillPonk, ModuleDef.SLOTS_DRAGONHATCH);
            });
        });
    }
    /** */
    playRedMod() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonFireAppear, ModuleDef.SLOTS_DRAGONHATCH, () => {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonFireSkill, ModuleDef.SLOTS_DRAGONHATCH);
        });
    }

    /** */
    playMomSleepIdle(index: number) {
        let str: string = EEFFECT.voxMamaIdle + index;
        tgx.AudioMgr.inst.audio.playEffect(str, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    playMomUnlock() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonMotherInfobarUnlock, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /**Tip */
    playMomAwakeTip() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.uiAlertDragonqueenAwake, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    playMomAwakeLoop() {
       this.momAwakeLoop();
    }
    private momAwakeLoopId:number;
    private momAwakeLoop() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonMotherAwakeLoop, ModuleDef.SLOTS_DRAGONHATCH, () => {
            this.momAwakeLoop();
        }).then((value)=>{
            this.momAwakeLoopId = value; 
        });
    }

    /** */
    playMomAwake() {
        tgx.AudioMgr.inst.audio.stopEffect(this.momAwakeLoopId, EEFFECT.dragonMotherAwakeLoop, ModuleDef.SLOTS_DRAGONHATCH);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonMotherAwake, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    playMomAwakeOut() {
        tgx.AudioMgr.inst.audio.stopEffect(this.momAwakeLoopId, EEFFECT.dragonMotherAwakeLoop, ModuleDef.SLOTS_DRAGONHATCH);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonMotherOut, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    playMomStomp() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonMotherShuffleStart, ModuleDef.SLOTS_DRAGONHATCH);
    }
    /** */
    playMomBreathFire() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.dragonMotherShuffleStop, ModuleDef.SLOTS_DRAGONHATCH);
    }

    /** */
    playTotalWin(med: boolean = false) {
        if (med) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeInfobarTotalwinMed, ModuleDef.SLOTS_DRAGONHATCH);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeInfobarTotalwin, ModuleDef.SLOTS_DRAGONHATCH);
        }
    }
    /** */
    playWin(med: boolean = false) {
        if (med) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeMedwin, ModuleDef.SLOTS_DRAGONHATCH);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.prizeSmallwin, ModuleDef.SLOTS_DRAGONHATCH);
        }
    }

    /**bigwin */
    private bigwinId: number;
    playBigwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_DRAGONHATCH).then((value) => {
            this.bigwinId = value;
        });
    }
    /**bigwin */
    playBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinId, EEFFECT.bgm_bigwin_main, ModuleDef.SLOTS_DRAGONHATCH);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_end, ModuleDef.SLOTS_DRAGONHATCH, () => {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.amb_windbell, ModuleDef.SLOTS_DRAGONHATCH).then((value)=>{
                this.windbellId = value;
            });
        });
    }
    /**bigwin */
    private windbellId:number;
    playBigwinOut() {
        tgx.AudioMgr.inst.audio.stopEffect(this.windbellId, EEFFECT.amb_windbell, ModuleDef.SLOTS_DRAGONHATCH);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgm_bigwin_out, ModuleDef.SLOTS_DRAGONHATCH);
    }
}

export enum EEFFECT {
    barRiseA = "sound/dhaudio_extract/barRiseHigher",//1~3
    barRiseB = "sound/dhaudio_extract/barRiseB",//4
    barRiseHigher = "sound/dhaudio_extract/barRiseHigher",//
    dragonEarthAppear = "sound/dhaudio_extract/dragonEarthAppear",//
    dragonEarthSkill = "sound/dhaudio_extract/dragonEarthSkill",//
    dragonWaterAppear = "sound/dhaudio_extract/dragonWaterAppear",//
    dragonWaterSkill = "sound/dhaudio_extract/dragonWaterSkill",//
    dragonWaterSkillPonk = "sound/dhaudio_extract/dragonWaterSkillPonk",//
    dragonFireAppear = "sound/dhaudio_extract/dragonFireAppear",//
    dragonFireSkill = "sound/dhaudio_extract/dragonFireSkill",//
    dragonMotherAwake = "sound/dhaudio_extract/dragonMotherAwake",//
    dragonMotherAwakeLoop = "sound/dhaudio_extract/dragonMotherAwakeLoop",//
    uiAlertDragonqueenAwake = "sound/dhaudio_extract/uiAlertDragonqueenAwake",//Tip
    dragonMotherInfobarUnlock = "sound/dhaudio_extract/dragonMotherInfobarUnlock",//
    dragonMotherOut = "sound/dhaudio_extract/dragonMotherOut",//
    dragonMotherShuffleStart = "sound/dhaudio_extract/dragonMotherShuffleStart",//
    dragonMotherShuffleStop = "sound/dhaudio_extract/dragonMotherShuffleStop",//
    dragonMotherShuffleSymWin = "sound/dhaudio_extract/dragonMotherShuffleSymWin",//
    dragonMotherSigh = "sound/dhaudio_extract/dragonMotherSigh",//
    dragonMotherSighEnd = "sound/dhaudio_extract/dragonMotherSighEnd",//
    symWild = "sound/dhaudio_extract/symWild",//wild spawn +""01 02 03
    payoutSelect = "sound/dhaudio_extract/payoutSelect",//
    symShrink = "sound/dhaudio_extract/symShrinkA",//
    prizeInfobarTotalwin = "sound/dhaudio_extract/prizeInfobarTotalwin",//
    prizeInfobarTotalwinMed = "sound/dhaudio_extract/prizeInfobarTotalwinMed",//
    prizeMedwin = "sound/dhaudio_extract/prizeMedwin",//
    prizeSmallwin = "sound/dhaudio_extract/prizeSmallwin",//
    spinBtn = "sound/dhaudio_extract/spinBtnA",//
    spinReelStopNormal = "sound/dhaudio_extract/spinReelStopNormalA",//
    spinReelStopQuick = "sound/dhaudio_extract/spinReelStopQuick",//
    voxMamaIdle = "sound/dhaudio_extract/voxMamaIdle",// +2345
    bgm_bigwin_main = "sound/bgm_bigwin_main",//bigwin
    bgm_bigwin_end = "sound/bgm_bigwin_end",//bigwin
    amb_windbell = "sound/amb_windbell",//bigwin
    bgm_bigwin_out = "sound/bgm_bigwin_out",//bigwin
    bgm_mg_lv_04_intro = "sound/bgm_mg_lv_04_intro",//bg4
}
